// --------------------------------------------------
// Generic Reset
// --------------------------------------------------

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  // font-family: 'Oswald', sans-serif;
  font-family: 'Barlow', sans-serif;
  font-weight: 300;
  box-sizing: border-box;
}